body {
   font: 14px "Century Gothic", Futura, sans-serif;
   margin: 20px;
   background-color: #151626;
   color: #FFF;
}

.board-row:after {
   clear: both;
   content: "";
   display: table;
}

.status {
   margin-bottom: 10px;
}

.square {
   background: rgb(33, 23, 53);
   color: #F9f9f9;
   border: 1px solid #999;
   float: left;
   font-size: 34px;
   font-weight: bold;
   line-height: 34px;
   height: 74px;
   width: 74px;
   margin-right: -1px;
   margin-top: -1px;
   padding: 0;
   text-align: center;
}

.game-board{
   margin-top: 30px;
}

.square:focus {
   outline: none;
}

.kbd-navigation .square:focus {
   background: #ddd;
}

.game {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.game-info {
   margin-top: 10px;
}

ol {
   list-style: none;
   margin: 10px 0;
   padding: 0;
}

.history-button {
   border: 1px solid #1b55ca;
   padding: 4px 8px;
   font-size:14px;
   background-color: transparent;
   color: #f9f9f9;
   border-radius: 2px;
   margin-bottom: 4px;
}
.history-button:hover {
   cursor: pointer;
   transform: scale(1.2);
   background-color: #1b55ca;
}

.game-status {
   font-size:16px;
   letter-spacing: 1px;
}

.winner-square {
   background-color: rgb(121, 14, 170);
}